import React from 'react';
import participationService from '../services/participation-service'

class UserEvents extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            message: "",
            events: null
        }
    }

    async componentDidMount() {
        const events = await participationService.getWydarzenia();
        console.log(events);
        if(events.status == 204) this.setState({message : "Nie bierzesz udziału w żadnym wydarzeniu !"});
    }

    render() {
        return(<div id="eventMenu" className="container bg-light border rounded border-dark">
        <header>
            <h1>Twoje wydarzenia</h1>
            <hr className="my-4" />

        </header>
            {this.state.message}
    </div>)
    }
}

export default UserEvents;